.catering {
  width: 100%;
  position: relative;
}

.catering-block {
  width: 100%;
  position: relative;
}

.no-webp .catering-block::after {
  background: url("./../img/catering/banner.png") no-repeat; }

.catering-block::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -5;
  width: 100%;
  height: 100%;
  background-size: cover;
}

.catering-block::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -3;
  width: 50%;
  height: 100%;
  background: #06417d;
  opacity: 0.8;
}

