@media (max-width: 1440px) {
	.business-block__add {
		font-family: PTSans;
		font-style: normal;
		font-weight: normal;
		font-size: 50px;
		line-height: 50px;
		text-align: left;
		color: #021245;
		margin: 70px 0 10px 0;
	}
	.business-block__title {
		margin: 0 0 90px 0;
		font-family: Prata;
		font-style: normal;
		font-weight: normal;
		font-size: 64px;
		line-height: 70px;
		color: #021245;
	}
}
@media (max-width: 1430px) {
	.business-block__client-w .swiper-slide {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.business-block__list {
		justify-items: center;
	}
}
@media (max-width: 1300px) {
	.business-block__title {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
	}
	.business-block__titles {
		margin: 35px 0 0 0;
	}
}
@media (max-width: 1200px) {
	.business-block__containers {
		padding: 0 20px;
	}
}
@media (max-width: 1150px) {
	.business-block__infos {
		display: flex;
		flex-direction: column-reverse;
		align-items: flex-start;
		justify-content: flex-start;
	}
	.business-block__infos-photo {
		width: 100%;
	}
	.business-block__infos-list {
		width: 100%;
		margin: 55px 0 0 0;
	}
}
@media (max-width: 1000px) {
	.business-block__list {
		justify-items: center;
	}
	.business-block__list {
		padding: 60px 20px;
	}
	.business-block__title {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		gap: 35px;
	}
	.business-block__add {
		font-family: PTSans;
		font-style: normal;
		font-weight: normal;
		font-size: 33px;
		line-height: 50px;
		text-align: left;
		color: #021245;
		margin: 70px 0 10px 0;
	}
}
